      flex-direction: column;
  -ms-flex: 1 1;
      flex: 1 1;
}


.send-message-form{
  padding: 10px 15px 10px 15px;
  display: -ms-flexbox;
  display: flex;
}

.send-message-form div {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-right: 5px;
}


.online-list-item {
  width: 10px;
